body{
  margin: 0px;
  padding: 0px;
}

li{
  list-style: none;
}

.header{
  background: whitesmoke;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  text-align: center;
  color: #4a4a4a;
  padding: 8px;
}

.userDetailWrapper{
  width: 46.875em;
  margin: 0px auto;
}

.users-list ul, .userDetailWrapper ul{
  padding: 5px;
  border: 1px solid #e9e7e7;
}

.users-list ul li, .userDetailWrapper ul li{
  padding: 20px;
  border-bottom: 1px solid #e9e7e7;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.1);
  
}

.user-image{
  float: left;
  width: 10%;
  text-align: center;
}

.user-image img{
  border: 1px solid #c2c1c1;
  border-radius: 50%;
  width: 50%;
  height: 50%;
}

.user-name{
  float: left;
  width: 90%;
  font-size: 18px;
  color: #4a4a4a;
  line-height: 35px;
  
}

.user-name p{
  cursor: pointer;
}

.loader{
  border: 8px solid #f3f3f3;
  border-top: 8px solid #3498db;
  border-radius: 50%; 
  width: 45px;
  height: 45px;
  margin: 10px auto;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.clearfix::after {
  display: block;
  content: "";
  clear: both;
}

.headerWrapper{
  height: 200px;
  background: rgb(182, 10, 10);
  width: 100%;
}

.imageWrapper{
  height: 150px;
  width: 150px;
  margin: 0px auto;
}

.headerWrapper img{
  border: 1px solid #c2c1c1;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  margin-top: 20px;
}

.keys{
  float: left;
  width: 50%;
}

.values{
  float: left;
  width: 50%;
}

.userDetailWrapper ul li{
  font-size: 18px;
  color: #4a4a4a;  
}

button{
  background: rgb(182, 10, 10);
  color: #f3f3f3;
  width: 10%;
  padding: 10px;
  border: none;
  font-size: 14px;
  float: right;
  margin-top: 15px;
  margin-right: 15px;
}

button:hover{
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
}

@media only screen and (max-width: 768px){
  .user-name{
    line-height: 5px;
    font-size: 16px;
  }

  .userDetailWrapper{
    width: 48em;
  }

  .userDetailWrapper ul li{
    font-size: 16px;    
  }
}

@media only screen and (max-width: 375px){
  .userDetailWrapper{
    width: 23.43em;
  }

  .user-name{
    line-height: 0px;
    font-size: 12px;
  }

  .userDetailWrapper ul li{
    font-size: 12px;    
  }

  button{
    width: 20%;
    font-size: 10px;
  }
}
